.contact-preview img{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    -o-object-fit: cover;
       object-fit: cover;
}


.contact-preview {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: start;
    -ms-flex-align: center;
        align-items: center;
    color: #ffffff
}

.contact-preview .contact-info {
    margin-left: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: start;
    font-size: 15px;
}

.contact-info div {
    padding: 10px;
}

@media (min-width: 750px){
    .contact-preview .contact-info {
        font-size: 25px;;
    }
}


